import React from "react";

const ScatterChartBetaRandomLazy = React.lazy(() => import("./ScatterChartBetaRandom.js"))
const ScatterChartBetaMoreThanOneLazy = React.lazy(() => import("./ScatterChartBetaMoreThanOne.js"))
const ScatterChartBetaLessThanOneLazy = React.lazy(() => import("./ScatterChartBetaLessThanOne.js"))
const ScatterChartBetaBelowZeroLazy = React.lazy(() => import("./ScatterChartBetaBelowZero.js"))
const ScatterChartBetaBondLazy = React.lazy(() => import("./ScatterChartBetaBond.js"))
const LineDifferentBetasLazy = React.lazy(() => import("./LineDifferentBetas.js"))
const PerpetuityLazy = React.lazy(() => import("./Perpetuity.js"))
const PricegordonLazy = React.lazy(() => import("./PriceGordon.js"))
const QuizImporterForwardLazy = React.lazy(() => import("./QuizImporterForward.js"))
const QuizExporterForwardLazy = React.lazy(() => import("./QuizExporterForward.js"))

const ChartCallOptionHolderLazy = React.lazy(() => import("./ChartCallOptionHolder.js"))
const ChartCallOptionWriterLazy = React.lazy(() => import("./ChartCallOptionWriter.js"))
const ChartPutOptionHolderLazy = React.lazy(() => import("./ChartPutOptionHolder.js"))
const ChartPutOptionWriterLazy = React.lazy(() => import("./ChartPutOptionWriter.js"))



const ChartLineMarginalAnalysesLazy = React.lazy(() => import("./ChartLineMarginalAnalyses.js"));
const ChartLineProductionLeverage = React.lazy(() => import("./ChartLineProductionLeverage.js"));

const ChartMarginalQuizLazy = React.lazy(() => import("./ChartMarginalQuiz.js"))

const LineExportImportPortfolioLazy = React.lazy(() => import("./LineExportImportPortfolio.js"))

const ScatterCMLFormulaLazy = React.lazy(() => import("./ScatterCMLFormula.js"))
const ScatterSMLFormulaLazy = React.lazy(() => import("./ScatterSMLFormula.js"))


export default (props) => {
  const isSSR = typeof window === "undefined"
  return <>
    {!isSSR && (
      <React.Suspense fallback={<div />}>
        {props.type === 'scatterchartbetarandom' ? <ScatterChartBetaRandomLazy /> : null}
        {props.type === 'scatterchartbetamorethanone' ? <ScatterChartBetaMoreThanOneLazy /> : null}
        {props.type === 'scatterchartbetalessthanone' ? <ScatterChartBetaLessThanOneLazy /> : null}
        {props.type === 'scatterchartbetabelowzero' ? <ScatterChartBetaBelowZeroLazy /> : null}
        {props.type === 'scatterchartbetabond' ? <ScatterChartBetaBondLazy /> : null}
        {props.type === 'linedifferentbetaslazy' ? <LineDifferentBetasLazy /> : null}
        {props.type === 'perpetuity' ? <PerpetuityLazy /> : null}
        {props.type === 'pricegordon' ? <PricegordonLazy /> : null}
        {props.type === 'quizimporterforward' ? <QuizImporterForwardLazy /> : null}
        {props.type === 'quizexporterforward' ? <QuizExporterForwardLazy /> : null}

        {props.type === 'chartcalloptionholder' ? <ChartCallOptionHolderLazy /> : null}
        {props.type === 'chartcalloptionwriter' ? <ChartCallOptionWriterLazy /> : null}
        {props.type === 'chartputoptionholder' ? <ChartPutOptionHolderLazy /> : null}
        {props.type === 'chartputoptionwriter' ? <ChartPutOptionWriterLazy /> : null}

        
        {props.type === 'chartlinemarginalanalyses' ? <ChartLineMarginalAnalysesLazy /> : null}
        {props.type === 'chartlineproductionleverage' ? <ChartLineProductionLeverage /> : null}
        
        {props.type === 'chartmarginalquiz' ? <ChartMarginalQuizLazy /> : null}

        {props.type === 'lineexportimportportfolio' ? <LineExportImportPortfolioLazy /> : null}

        {props.type === 'scattercmlformula' ? <ScatterCMLFormulaLazy /> : null}
        {props.type === 'scattersmlformula' ? < ScatterSMLFormulaLazy /> : null}        
        
      </React.Suspense>
    )}
  </>
}

